.content-container {
  display: flex;
  flex-direction: column;
  gap: 8px;
  max-width: 359px;
  margin: 0 auto;
}

.card {
  padding: 24px 16px 16px;
  max-width: 600px;
  background-color: #FFF;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  border-radius: 12px;
  text-align: center;
}